.formInAboutUs {
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    align-items: center;
    border-radius: 24px;
    border: solid 1px #ddd;

    .formHeaderText {
        font-size: 42px;
        font-weight: 600;
        color: white;
    }

    #subject {
        width: 240px;
        height: 40px;
        padding: 0 10px;
        outline: none;
        font-size: 15px;
        color: black;
        border-radius: 20px;
    }

    #email {
        width: 240px;
        height: 40px;
        padding: 0 10px;
        outline: none;
        font-size: 15px;
        color: black;
        border-radius: 20px;
    }

    .formBottom {
        width: 84%;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        #textMessage {
                width: 160px;
                height: 40px;
                padding: 0 10px;
                outline: none;
                font-size: 15px;
                color: white;
                border: none; 
                border-bottom: solid 2px white;
                background-color: transparent;
        
                &::placeholder {
                    font-style: italic;
                }
            }
            .formSendButton{
                width: 60px;
                height: 40px;
                // border: none;
                border-radius: 3px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 15px;
                background-color: transparent;
                font-weight: 400;
                color: white;
                border: solid 1px white;
            }
    }

}